import {Button, Divider, Form, Input, InputNumber, Upload, message} from 'antd';
import { useState } from 'react';
import "./index.css";
import {API_URL} from "../config/constants"
import axios from 'axios';
import {useHistory} from "react-router-dom"

function UploadPage(){
     const [imageUrl, setImageUrl] = useState(null);
     const history = useHistory();
     const onSubmit = (values) => {
          axios.post(`${API_URL}/products`,{
               name : values.name,
               description : values.description,
               seller : values.seller,
               price : parseInt(values.price),
               imageUrl : imageUrl
          }).then((result)=>{
               console.log(result);
               history.replace('/')//   .
          }).catch((error)=>{
            console.log(error);
            message.error(` . ${error.message}`)
           });
     };
     const onChangeImage = (info) =>{ // 
          if(info.file.status === 'uploading'){//   
               return;
          }
          if(info.file.status === 'done'){//  
               const response = info.file.response;
               const imageUrl = response.imageUrl; //imageUr    useState 
               setImageUrl(imageUrl); //      onChange  onChangeImage  info()  setImageUrl(status)  .
          }
     }

     return (
      <div id ="upload-container">
           <Form name =" " onFinish={onSubmit}>
             <Form.Item 
             name="upload" 
             label={<div className="upload-label"> </div>}
             >
               <Upload 
               name='image'
               action={`${API_URL}/image`}
               listType="picture"
               showUploadList={false}
               onChange={onChangeImage}
               >
                {imageUrl ? (//imageUr   img src  
                <img id="upload-img" src={`${API_URL}/${imageUrl}`}/>
                ) : (//   
                     <div id = "upload-img-placeholder">
                     <img src ="/images/icons/camera.png" />
                     <span>  </span>
                   </div>
                 )}

              
               </Upload>
             </Form.Item>
             <Divider />
             <Form.Item label={<div
             className="upload-label"
             
             > </div>}
             name="seller"
             rules={[{required : true, message : '  '}]}
             >
                  <Input className="upload-name" size="large" placeholder=" ."/>
             </Form.Item>
            <Divider />
            <Form.Item
              name ="name"
              label={<div className="upload-label"> </div>}
              rules={[{required : true, mwssage : '  .'}]}
            >
            <Input className="upload-name" size="large" placeholder="  " />
            </Form.Item>
            <Divider />
            <Form.Item
               name="price"
               label={<div className="upload-label"> </div>}
               rules={[{required : true, message : "  ."}]}>
                <InputNumber defaultValue={0}
                className ="upload-price" 
                size="large" />
               </Form.Item>
            <Divider />
            <Form.Item
               name="description"
               label={<div className="upload-label"> </div>}
               rules={[{required : true, message : "  ."}]}>
               <Input.TextArea 
               size="large" 
               id="product-description" 
               showCount maxLength={300}
               placeholder="  "/>
            </Form.Item>
            <Form.Item>
                 <Button id ="submit-button" size="large" htmlType="submit" >
                     
                 </Button>
            </Form.Item>

           </Form>
      </div>
     );

}

export default UploadPage;